import { html } from 'lit';
import { fixture, expect } from '@open-wc/testing';
import { SvgEditor } from '../src/parts/templates/SvgEditor.js';
import '../src/svg-editor.js';

describe('SvgEditor', () => {
  it('has a default title "Hey there" and counter 5', async () => {
    const el = await fixture<SvgEditor>(html`<svg-editor></svg-editor>`);

    expect(el.title).to.equal('Hey there');
    expect(el.counter).to.equal(5);
  });

  it('increases the counter on button click', async () => {
    const el = await fixture<SvgEditor>(html`<svg-editor></svg-editor>`);
    el.shadowRoot!.querySelector('button')!.click();

    expect(el.counter).to.equal(6);
  });

  it('can override the title via attribute', async () => {
    const el = await fixture<SvgEditor>(
      html`<svg-editor title="attribute title"></svg-editor>`
    );

    expect(el.title).to.equal('attribute title');
  });

  it('passes the a11y audit', async () => {
    const el = await fixture<SvgEditor>(html`<svg-editor></svg-editor>`);

    await expect(el).shadowDom.to.be.accessible();
  });
});
